<template>
	<div id="background">
		<div id="result-box">
			<h1>在售手机列表</h1>

			<Card v-for="group of groups" class="card">
        <p slot="title">{{group.name}}--{{group.subtitle}}</p>
        <Button type="primary" shape="circle" size="small" slot="extra" @click="enterGroup(group.categoryId)">加入购物车</Button>
        <!-- <Input slot="extra"></Input> -->
        <!-- <Button type="error" shape="circle" size="small" slot="extra">退出小组</Button> -->
        <!-- <img :src="group.mainImage"> -->
        <p>金额：{{group.price}}</p>
      </Card>
		</div>
	</div>
</template>
<script>
import router from '../../router'
import { getJoinedGroups, addNewProduct } from '../../api/api'
export default {
	mounted: function() {
		this.getGroups()
	},
	methods: {
		getGroups: function() {
    let options = {
      username: window.localStorage.getItem('username'),
      pageNum: 1,
      pageSize: 10,
    }
    getJoinedGroups(options)
      .then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            console.log(res.data.data.list)
            this.groups = res.data.data.list
          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info(`未知错误, 无法获取数据，状态码:${res.status}`)
        }
      })
  	},
  	enterGroup: function(id) {
      let options = {
        productId: id,
        quantity: 1,
      }
  		addNewProduct(options).then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            this.$Message.info("添加购物车成功")
            this.$emit('change','+', 1)
          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info("未知错误")
        }
      })

  	}
	},
	data: function() {
		return {
			groups: [
				{
          title: 'abc',
          messges: 'abv'
        }
			]
		}
	}
}
</script>
<style scoped>
#result-box {
	width: 900px;
  margin: 0 auto;
}
</style>


