<template>
  <div id="background">
    <!-- <div id="search-box">
      <strong>小组搜索</strong>
      <div>
        <Input v-model="value" icon="search" @on-enter="searchGroupsByTitle"></Input>
      </div>
    </div> -->
 <!--    <div id="progress-box" v-id="isSearching">
      <Progress :percent="45" status="active" :stroke-width="5" hide-info></Progress>
    </div> -->
    <div id="results-box">
      <Card v-for="(group, index) of groups" class="card">
        <p slot="title">收货人：{{group.receiverName}}</p>
        <Button type="primary" shape="circle" size="small" slot="extra" @click="joinAGroup(group.id, index)">删除地址</Button>
        <p>{{group.receiverProvince}} {{group.receiverCity}} {{group.receiverDistrict}} {{group.receiverAddress}}</p>
      </Card>
    </div>
    <div id="login-box">
      <form>
        <table>
          <tr class="first-title">收货人</tr>
          <tr><Input v-model="name" style="width: 280px"></Input></tr>
          <tr>收货人号码</tr>
          <tr>
            <Input v-model="phone" style="width: 280px"></Input>
          </tr>
          <tr>收货人手机号码</tr>
          <tr><Input v-model="mobilePhone" style="width: 280px"></Input></tr>
          <tr>省</tr>
          <tr>
            <Input v-model="province"  style="width: 280px"></Input>
          </tr>
          <tr>城市</tr>
          <tr><Input v-model="city" style="width: 280px"></Input></tr>
          <tr>区</tr>
          <tr><Input v-model="district" style="width: 280px"></Input></tr>
          <tr>详细地址</tr>
          <tr><Input v-model="address" style="width: 280px"></Input></tr>
          <tr>邮政编码</tr>
          <tr><Input v-model="zip" style="width: 280px"></Input></tr>
          <tr><Button type="success" size="large" @click="postNewAddress">添加</Button></tr>
        </table>
      </form>
    </div>
  </div>
</template>
<script>
import router from '../../router'
import { searchGroups, joinGroup, getShippingAddress, addNewAddress } from '../../api/api'
export default {
  data: function() {
    return {
      isSearching: true,
      groups: [],
      totalPage: 0,
      value: '',
      name: '',
      phone: '',
      mobilePhone: '',
      province: '',
      city: '',
      district: '',
      address: '',
      zip: '',
    }
  },
  methods: {
    searchGroupsByTitle: function() {
      let options = {
        titleCondition: this.value,
        pageNum: 1,
        pageSize: 10,
      }
      getShippingAddress(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              console.log(res.data.data.list)
              this.groups = res.data.data.list
              this.totalPage = res.data.total
            } else {
              this.$Message.info(res.data.msg)
            }
          } else {
            this.$Message.info(`未知错误,状态码:${res.status}`)
          }
        })
    },
    joinAGroup: function(title, index) {
      let options = {
        shippingId: title
      }
      joinGroup(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              this.$Message.info(res.data.msg)
              // router.push('/home/searchGroups')
              this.groups.splice(index, 1)
            } else {
              this.$Message.info(res.data.msg)
            }
          } else {
            this.$Message.info(`未知错误,状态码:${res.status}`)
          }
        })
    },
    getAddress: function() {
      let options = {
        pageNum: 1,
        pageSize: 10,
      }
      getShippingAddress(options).then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            this.groups = res.data.data.list
          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info('获取地址失败')
        }
      })
    },
    postNewAddress: function() {
      let options = {
        receiverName: this.name,
        receiverPhone: this.phone,
        receiverMobile: this.mobilePhone,
        receiverProvince: this.province,
        receiverCity: this.city,
        receiverDistrict: this.district,
        receiverAddress: this.address,
        receiverZip: this.zip,
      }

      addNewAddress(options).then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            this.$Message.info(res.data.msg)
            router.push("/home")
          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info("未知错误")
        }
      })
    }
  },
  mounted: function() {
    this.getAddress()
  }
}
</script>
<style scoped>
div {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
#search-box {
  width: 400px;
  margin: 0 auto;
  margin-top: 30px;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
  background: -webkit-linear-gradient(left top, #66f9cf, #2d8cf0);
}
#search-box div {
  margin: 0 auto;
  width: 300px;
  margin-bottom: 10px;
}
#search-box strong {
  color: #FFFFFF;
}
#progress-box {
  width: 400px;
  margin: 0 auto;
}
#page {
  margin-top: 10px;
}
#results-box {
  width: 900px;
  margin: 0 auto;
}
.card {
  margin-top: 5px;
}
#page {
  margin-top: 10px;
}
#login-box, #message-box {
    background-color: #FFFFFF;
    width: 400px;
    margin: 0 auto;
    box-shadow:0 1px 2px rgba(0,0,0,0.3);
    border-radius: 5px;
  }
  #login-box table {
    width: 400px;
    padding-top: 20px;
    margin-top: 10px;
  }
  #login-box tr {
    display: block;
    margin-bottom: 10px;
  }
</style>


