<template>
  <div id="background">
    <div id="messages">
      <Row class="hidden">dfadasd</Row>
      <Row>
        <Col span="1">
          <div  @click="clickLeft">
            <Icon type="chevron-left" size="22" class="cursor">
          </Icon>
          </div>
        </Col>
        <Col span="22">
          <!-- <p>{{messageList.length != 0 ? messageList[currentMsg].title : '暂无最新消息'}}</p> -->
          <p>最新Iphone降价促销</p>
        </Col>
        <Col span="1">
          <div  @click="clickRight">
            <Icon type="chevron-right" size="22" class="cursor" @click="clickRight">
          </Icon>
        </div>
        </Col>
      </Row>
    </div>
    <div id="others">
      
    </div>
    <div id="groups">
      <div id="groups-title">
        <strong>热销</strong>排行榜
      </div>
      <!-- <Table :row-class-name="rowClassName" :columns="columns" :data="tableData"></Table> -->
      <Card v-for="d of tableData" class="card">
        <p slot="title">{{d.name}} {{d.subtitle}}</p>
        <!-- <Button type="primary" shape="circle" size="small" slot="extra" @click="enterGroup(group.categoryld)">加入购物车</Button> -->
        <!-- <Input slot="extra"></Input> -->
        <!-- <Button type="error" shape="circle" size="small" slot="extra">退出小组</Button> -->
      </Card>
    </div>
  </div>
</template>
<script>
import router from '../../router'
import { getMessages, getJoinedGroups } from '../../api/api'
export default {
props: ['username'],
mounted: function() {
  this.getNewMessages()
  this.getGroups()
},
methods: {
  clickLeft: function() {
    if(this.currentMsg == 0) {
      return
    }
    this.currentMsg--
  },
  clickRight: function() {
    if(this.currentMsg == (this.messageList.length - 1)) {
      return
    }
    this.currentMsg++
  },
  rowClassName: function(row, index) {
    if (index === 1) {
            return 'demo-table-info-row';
        } else if (index === 3) {
            return 'demo-table-error-row';
        }
        return '';
  },
  getNewMessages: function() {
    let options = {
      pageNum: 1,
      pageSize: 10,
    }
    getMessages(options)
      .then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            this.messageList = res.data.data.list
          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info(`未知错误, 无法获取最新消息，状态码:${res.status}`)
        }
      })
  },
  getGroups: function() {
    let options = {
      username: window.localStorage.getItem('username'),
      pageNum: 1,
      pageSize: 5,
    }
    getJoinedGroups(options)
      .then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            console.log(res.data.data.list)
            this.tableData = res.data.data.list
          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info(`未知错误, 无法获取最常访问小组，状态码:${res.status}`)
        }
      })
  },
  enterGroup: function(title) {
      router.push(`/home/groups/${title}`)
    }
},
data: function() {
  return {
    currentMsg: 0,
    messageList: [],
    columns: [
      {
        title: '小组编号',
        key: 'id',

      },
      {
        title: '小组名',
        key: 'title',

      },
      {
        title: '操作',
        key: 'action',
        width: 200,
        align: 'center',
        render: (h, params) => {
            return h('div', [
                h('Button', {
                    props: {
                        type: 'primary',
                        size: 'small'
                    },
                    style: {
                        marginRight: '5px'
                    },
                    on: {
                        click: () => {
                            console.log(params.row.title)
                            this.enterGroup(params.row.title)
                        }
                    }
                }, '进入小组'),
                h('Button', {
                    props: {
                        type: 'error',
                        size: 'small'
                    },
                    on: {
                        click: () => {
                            this.$Meesage.info("莫名情况没有反应")
                        }
                    }
                }, '退出小组')
            ]);
        }
      }
    ],
    tableData: [
      // {
      //   group_id: '1',
      //   group_name: '测试小组1'
      // },
      // {
      //   group_id: '2',
      //   group_name: '测试小组2'
      // },
      // {
      //   group_id: '3',
      //   group_name: '测试小组3'
      // },
      // {
      //   group_id: '4',
      //   group_name: '测试小组4'
      // },
      // {
      //   group_id: '5',
      //   group_name: '测试小组5'
      // },
    ]

  }
}
}
</script>
<style scoped>
#messages, #groups, #others {
  width: 90%;
  height: 70px;
  margin: 0 auto;
  margin-top: 20px;
  background-color: #FFFFFF;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
  position: relative;
}
#others {
  height: 100px;
  background: -webkit-linear-gradient(left top,#2d8cf0,#66f9cf);
}
#groups-title {
  font-size: 16px;
}
#groups {
  height: 500px;
}
#messages p {
  font-size: 22px;
}
.hidden {
  visibility: hidden;
}
.cursor:hover {
  cursor: pointer;
}
.ivu-table .demo-table-info-row td{
    background-color: #2db7f5;
    color: #fff;
}
.ivu-table .demo-table-error-row td{
    background-color: #ff6600;
    color: #fff;
    }
</style>


