import axios from 'axios'

let base = `http://localhost:8080`

const request = (url,options={},method='get',headers={}) => {
	let key = ~['delete','get','head'].indexOf(method) ? 'params' : 'data'
	return axios(Object.assign({url:url,method:method,validateStatus:false,headers:headers},{[key]:options})).
			then(response=>response)
}
const loginRequest = (options) => {
	console.log(`${base}/user/login.do`)
	console.log(options)
	return request(`${base}/user/login.do`,options,'post')
}
