// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import router from './router'
import iView from 'iview';
import 'iview/dist/styles/iview.css';

Vue.config.productionTip = false
Vue.use(iView);
Vue.use(VueRouter)

/* eslint-disable no-new */
 router.beforeEach((to,from,next) => {
 	let user = window.localStorage.getItem('username')

 	if (!user && (to.path == '/home' || to.path == '/home/changePassword' 
 			|| to.path == '/home/user/profile' || to.path == '/home/messages'
  				|| to.path == '/home/searchGroups' || to.path == '/home/addGroup' 
 					|| to.path == '/home/groups' || to.path == '/home/groups/:group_name')) {
	next({path: '/user/login'})			
} else if (user && to.path == '/user/login') {
	next({path: '/home'})
} else {
		next()
}

})
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



