<template>
  <div>
    <div id="background"></div>
    <Menu mode="horizontal" theme="primary" active-name="1" @on-select="onSelect">
        <MenuItem name="0">
          <p style="font-size: 30px"><strong>手</strong>机<b>商</b>城</p>
        </MenuItem>
        <MenuItem name="1">
            <Icon type="home"></Icon>
            首页
        </MenuItem>
        <MenuItem name="2" v-if="hasLogin">
            <Icon type="ios-people"></Icon>
            在售手机列表
        </MenuItem>
        <MenuItem name="3">
            <Icon type="search" v-if="hasLogin"></Icon>
            管理收货地址
        </MenuItem>
        
        <Submenu name="5" id="login-or-logout">
            <template slot="title" v-if="!hasLogin">
                <Icon type="stats-bars"></Icon>
                未登录
            </template>
            <template slot="title" v-if="hasLogin">
                <Icon type="person"></Icon>
                {{username}}
            </template>
            <MenuGroup title="用户">
                <MenuItem name="5-1" v-if="!hasLogin">登陆</MenuItem>
                <MenuItem name="5-2" v-if="hasLogin">用户信息</MenuItem>
                <MenuItem name="5-3" v-if="hasLogin">修改密码</MenuItem>
                <MenuItem name="5-4" v-if="hasLogin">登出</MenuItem>
                <MenuItem name="5-5" v-if="!hasLogin">注册</MenuItem>
                <MenuItem name="5-6" v-if="hasLogin">查看订单</MenuItem>
            </MenuGroup>
        </Submenu>
        <MenuItem name="4" id="message" v-if="hasLogin">
            <Badge :count="messageCount" :overflow-count="totalPage">
                <Icon type="android-cart" size="26"></Icon>
            </Badge>
        </MenuItem>
        <!-- <MenuItem name="6" id="add-groups" v-if="hasLogin">
            <Icon type="plus-round"></Icon>
        </MenuItem> -->
    </Menu>
    <div id="main">
      <router-view :username="username" @change="changeCart"></router-view>
    </div>
    <!-- <footer>
      <div>
        <ul>
          <li><a href="">了解我们</a></li>
          <li><a href="">帮助</a></li>
        </ul>
        <ul>
          <li>各种信息...</li>
          <li>各种信息...</li>
          <li>各种信息...</li>
        </ul>
      </div>
    </footer> -->
  </div>
</template>
<script scoped>
import router from '../../router'
import { logoutRequest, getMessages } from '../../api/api'
export default {
  data: function() {
    return {
      hasLogin: false,
      username: '',
      id: '',
      messageCount: 100,
      totalPage: 0,
    }
  },
  mounted: function() {
    let username = window.localStorage.getItem('username')
    let id = window.localStorage.getItem('id')
    if(username != null && id != null) {
      this.hasLogin = true
      this.username = username
      this.id = id
    }
    this.getCartNum()
  },
  
  methods: {
    onSelect: function(name) {
      if(name == '1') {
        router.push('/')
      }
      if(name == '2') {
        router.push('/home/groups')
      }
      if(name == '5-1') {
        router.push('user/login')
      } else if(name == '5-2') {
        router.push('/home/user/profile')
      } else if(name == '5-3') {
        router.push('/home/changepassword')
      } else if(name == '5-4') {
        this.logout()
      } else if(name == '5-5') {
        router.push('/')
      } else if(name == '5-6') {
        router.push('/home/orders')
      }
      if(name == '3') {
        router.push('/home/searchGroups')
      }
      if(name == '4') {
        router.push('/home/messages')
      }
      if(name == '6') {
        router.push('/home/addGroup')
      }
    },
    logout: function() {
      logoutRequest()
        .then(res=>{
          if(res.status == 200){
            if(res.data.status == 1) {
              this.$Message.info('登出成功')
              window.localStorage.clear()
              router.push('/user/login')
            } else {
              this.$Message.info('登出失败原因未知')
            }
          } else {
            this.$Message.info(`登出失败,状态码:${res.status}`)
          }
        })
    },
    getCartNum: function() {
      let options = {
      pageNum: 1,
      pageSize: 10,
      }
      getMessages(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              this.totalPage = res.data.data.total

            } else {
              this.$Message.info(res.data.msg)
            }
          } else {
            this.$Message.info(`未知错误，状态码:${res.status}`)
          }
        })
    },
    changeCart: function(operator, num) {
      if(operator == '+') {
        this.totalPage += num
      } else if(operator == '-') {
        this.totalPage -= num
      }
      // console.log(operator)
      // console.log(num)
    }
  }
}
</script>
<style>
#background {
  /*background-color: #F9F9F9;
  height: 100%;
  position: fixed;
  width: 100%;
  z-index: -1;*/
}
div {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
#message, #add-groups {
  float: right;
}
#login-or-logout {
  float:right;
}

</style>



