<template>
  <div id="background">
    <div id="info-box">
      <h2>小组信息</h2>
      <div>
        {{group.title}}
      </div>
      <div>
        {{group.messges}}
      </div>
    </div>
    <div id="member-box">
      <h2>小组用户</h2>
      <div>
        {{group.member}}
      </div>
    </div>
    <div id="upload-box">
      <Upload
            multiple
            type="drag"
            action="#">
            <div style="padding: 20px 0">
                <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                <p>点击或拖拽上传文件</p>
            </div>
        </Upload>
    </div>
  </div>
</template>
<script>
import { getGroupInfo } from '../../api/api'
export default {
  mounted: function() {
    console.log(this.$route.params.group_name)
    this.getAGroupInfo(this.$route.params.group_name)
  },
  data: function() {
    return {
      group: {} 
    }
  },
  methods: {
    getAGroupInfo: function(aTitle) {
      let options = {
        title: aTitle,
      }
      getGroupInfo(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              this.group = res.data.data
            } else {
              this.$Message.info(res.data.msg)
            }
          } else {
            this.$Message.info(`未知错误,状态码:${res.status}`)
          }
        })
    }
  }
}
</script>
<style>
#info-box, #member-box {
  width: 1000px;
  height: 70px;
  margin: 0 auto;
  margin-top: 20px;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
}
#member-box {
  height: 100px;
}
#upload-box {
  width: 1000px;
  height: 70px;
  margin: 0 auto;
  margin-top: 20px;
}
</style>


