<template>
  <div id="background">
    <div id="add-group-box"> 
    	<h1>创建小组</h1>
      <table>
	      <tr class="first-title">小组名</tr>
	      <tr><Input v-model="groupName" placeholder="小组名不能为空" style="width: 280px"></Input></tr>
	      <tr>小组简介</tr>
	      <tr><Input v-model="groupSummary" type="textarea" :rows="4"></Input></tr>
	      <tr><Button type="success" size="large" :disabled="addGroupSubmitState" @click="createGroup">创建</Button></tr>
	    </table>
    </div>
  </div>
</template>
<script>
import router from '../../router'
import { addGroup } from '../../api/api'
export default {
  computed: {
  	addGroupSubmitState: function() {
  		if(this.groupName.length == 0) {
  			return true
  		}
  		return false
  	}
  },
  data: function() {
  	return {
  		groupName: '',
  		groupSummary: ''
  	}
  },
  methods: {
  	createGroup: function() {
  		let options = {
  			title: this.groupName,
  			message: this.groupSummary,
  		}
  		addGroup(options)
  			.then(res=>{
  				if(res.status == 200) {
  					if(res.data.status == 1) {
  						this.$Message.info("创建成功")
  						router.push('/home')
  					} else {
  						this.$Message.info(`创建失败,${res.data.msg}`)
  					}
  				} else {
  					this.$Message.info(`创建失败,状态码:${res.status}`)
  				}
  			})
  	}
  }
}
</script>
<style scoped>
div {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
#add-group-box {
  width: 500px;
  margin: 0 auto;
  margin-top: 30px;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
  
}
#page {
  margin-top: 10px;
}
#add-group-box table {
  width: 400px;
  padding-top: 20px;
  margin-top: 10px;
  display: block;
  margin: 0 auto;
  padding-bottom: 10px;
}
#add-group-box tr {
  display: block;
  margin-bottom: 10px;
 }
</style>


