import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/HelloWorld'
import UserLogin from '@/components/user/login'
import Home from '@/components/main/home'
import ForgetPassword from '@/components/user/forget_password'
import MainHome from '@/components/main/main_home'
import ChangePassword from '@/components/user/change_password'
import UserProfile from '@/components/user/profile'
import Messages from '@/components/messages/messages'
import SearchGroups from '@/components/groups/searchGroups'
import addGroup from '@/components/groups/addGroup'
import Groups from '@/components/groups/groups'
import MainGroup from '@/components/groups/mainGroup'
import Register from '@/components/user/register'
import Orders from '@/components/groups/orders'

Vue.use(Router)

export default new Router({
  routes: [
  	{
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/home',
      name: 'Home',
      children: [
        {
          path: '/',
          component: MainHome,
        },
        {
          path: 'changePassword',
          component: ChangePassword,
        },
        {
          path: 'user/profile',
          component: UserProfile,
        },
        {
          path: 'messages',
          component: Messages,
        },
        {
          path: 'searchGroups',
          component: SearchGroups
        },
        {
          path: 'addGroup',
          component: addGroup
        },
        {
          path: 'groups',
          component: Groups
        },
        {
          path: 'groups/:group_name',
          component: MainGroup
        },
        {
          path: 'orders',
          component: Orders
        }
      ],
      component: Home
    },
    {
      path: '/user/login',
      name: 'UserLogin',
      component: UserLogin
    },
    {
    	path: '/user/forgetpassword',
    	name: 'ForgetPassword',
    	component: ForgetPassword
    },
    {
      path: '/user/register',
      name: 'Register',
      component: Register
    }
  ]
})



