<template>
	<div id="background">
		<div id="result-box">
			<h1>订单总览</h1>

			<Card v-for="group of groups" class="card">
        <p slot="title">订单号：{{group.orderNo}}--{{group.productName}} 订单创建时间: {{group.createTime}} </p>
        <!-- <Button type="primary" shape="circle" size="small" slot="extra" @click="enterGroup(group.title)">加入购物车</Button> -->
        <!-- <Button type="error" shape="circle" size="small" slot="extra">退出小组</Button> -->
        <!-- <p></p> -->
        <!-- <img :src="group.productImage"> -->
        <p>金额：{{group.totalPrice}}</p>
      </Card>
		</div>
    <div id="page">
        <Page :total="totalPage" @on-change="changePage"></Page>
      </div>
	</div>
</template>
<script>
import router from '../../router'
import { getJoinedGroups, getOrders } from '../../api/api'
export default {
	mounted: function() {
		this.getGroups()
	},
	methods: {
		getGroups: function() {
    let options = {
      // username: window.localStorage.getItem('username'),
      pageNum: 1,
      pageSize: 10,
    }
    getOrders(options)
      .then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            console.log(res.data.data.list)
            this.groups = res.data.data.list
            this.totalPage = res.data.data.total
          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info(`未知错误, 无法获取数据，状态码:${res.status}`)
        }
      })
  	},
  	enterGroup: function(title) {
  		router.push(`/home/groups/${title}`)
  	},
    changePage: function(num) {
      let options = {
        pageNum: num,
        pageSize: 10,
      }
      getOrders(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              console.log(res.data.data.list)
              this.groups = res.data.data.list
              this.totalPage = res.data.data.total

            } else {
              this.$Message.info(res.data.msg)
            }
          } else {
            this.$Message.info(`未知错误，状态码:${res.status}`)
          }
        })
    }
	},
	data: function() {
		return {
			groups: [
				
			],
      totalPage: 0,
		}
	}
}
</script>
<style scoped>
#result-box {
	width: 900px;
  margin: 0 auto;
}
</style>


