<template>
  <div id="background">
    <!-- <div id="search-box">
      <strong>消息搜索</strong>
      <div>
        <Input v-model="value" icon="search" @on-enter="findMessage"></Input>
      </div>
    </div> -->
    <div>
    <Radio-group v-model="vertical" vertical>
      <Radio :label="address.id" v-for="address of addresss">
        <span>{{address.receiverAddress}}</span>
        <!-- <span>{{address.id}}</span> -->
      </Radio>
    </Radio-group>
  </div>
    <div id="messages-box">
      <Card v-for="(msg, index) of messageList" class="card">
        <p slot="title"> 产品ID:{{msg.productId}}</p>
        <!-- <Button type="error" shape="circle" size="small" slot="extra" @click="deleteAMessage(msg.title, index)">删除消息</Button> -->
        <p slot="extra">数量: {{msg.quantity}}</p>
        <p>{{msg.messges}}</p>
      </Card>
      <!-- <Button @click="order"></Button> -->
      <Button type="success" size="large" @click="order">下单</Button>
      <!-- <Card v-for="(a, index) of 10" class="card">
        <p slot="title">{{a}}</p>
        <Button type="error" shape="circle" size="small" slot="extra" @click="deleteAMessage(msg.title, index)">删除消息</Button>
        <p>{{index}}</p>
      </Card> -->
      <div id="page">
        <Page :total="totalPage" @on-change="changePage"></Page>
      </div>
    </div>
  </div>
</template>
<script>
import router from '../../router'
import { getMessages, searchMessage, deleteMessage, postOrder, getShippingAddress } from '../../api/api'
export default {
  mounted: function() {
    let options = {
      pageNum: 1,
      pageSize: 10,
    }
    getMessages(options)
      .then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            this.messageList = res.data.data.list
            this.totalPage = res.data.data.total

          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info(`未知错误，状态码:${res.status}`)
        }
      })
    this.getAddress()
  },
  data: function() {
    return {
      messageList: ['a'],
      totalPage: 0,
      value: '',
      addresss: [{id: 123}],
      vertical: '',
    }
  },
  methods: {
    changePage: function(num) {
      let options = {
        pageNum: num,
        pageSize: 10,
      }
      getMessages(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              console.log(res.data.data.list)
              this.messageList = res.data.data.list
              this.totalPage = res.data.data.total

            } else {
              this.$Message.info("未知错误")
            }
          } else {
            this.$Message.info(`未知错误，状态码:${res.status}`)
          }
        })
    },
    findMessage: function() {
      let options = {
        messageCondition: this.value,
        pageNum: 1,
        pageSize: 10,
      }
      searchMessage(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              this.totalPage = 0
              this.messageList = res.data.data.list
            } else {
              this.$Message.info(`${res.data.msg}`)
            }
          } else {
            this.$Message.info(`未知错误，状态码:${res.status}`)
          }
        })
    },
    deleteAMessage: function(messageTitle, index) {
      let options = {
        title: messageTitle,
      }
      deleteMessage(options)
        .then(res=>{
          if(res.status == 200) {
            if(res.data.status == 1) {
              this.messageList.splice(index, 1)
              this.$Message.info(`${res.data.msg}`)
            } else {
              this.$Message.info(`${res.data.msg}`)
            }
          } else {
            this.$Message.info(`未知错误，状态码:${res.status}`)
          }
        })
    },
    order: function() {
      if(this.vertical == '') {
        this.$Message.info('地址不能为空')
        return
      }
      if(this.messageList.length == 0) {
        this.$Message.info('购物车为空')
        return 
      }
      let options = {
        shippingId: this.vertical,
      }
      postOrder(options).then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            router.push('/home/orders')
          } else {
            this.$Message.info(res.data.msg)
          }
        } else {
          this.$Message.info("未知错误")
        }
      })
      // console.log(this.vertical == '')
    },
    getAddress: function() {
      let options = {
        pageNum: 1,
        pageSize: 10,
      }
      getShippingAddress(options).then(res=>{
        if(res.status == 200) {
          if(res.data.status == 1) {
            this.addresss = res.data.data.list
          } else {
            this.$Messgae.info(res.data.msg)
          }
        } else {
          this.$Message.info('获取地址失败')
        }
      })
    }
  }
}
</script>
<style scoped>
div {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
#messages-box {
  width: 90%;
  /*height: 600px;*/
  margin: 0 auto;
  margin-top: 30px;
  
}
#page {
  margin-top: 10px;
}
#search-box {
  width: 400px;
  margin: 0 auto;
  margin-top: 30px;
  box-shadow:0 1px 2px rgba(0,0,0,0.3);
  border-radius: 5px;
  background: -webkit-linear-gradient(left top, #66f9cf, #2d8cf0);
}
#search-box div {
  margin: 0 auto;
  width: 300px;
  margin-bottom: 10px;
}
#search-box strong {
  color: #FFFFFF;
}
.card {
  margin-top: 5px;
}
</style>


